<template>
  <div class="bar">
    <el-tooltip effect="dark" content="" placement="bottom" class="icon">
      <i class="el-icon-zoom-in" @click="changeEvent('zoomIn')"></i>
    </el-tooltip>
    <el-tooltip effect="dark" content="" placement="bottom">
      <i class="el-icon-zoom-out" @click="changeEvent('zoomOut')"></i>
    </el-tooltip>
    <el-tooltip effect="dark" content="" placement="bottom">
      <i class="el-icon-refresh-right" @click="changeEvent('rotateRight')"></i>
    </el-tooltip>
    <el-tooltip effect="dark" content="" placement="bottom">
      <i class="el-icon-refresh-left" @click="changeEvent('rotateLeft')"></i>
    </el-tooltip>
    <el-tooltip effect="dark" content="" placement="bottom">
      <i class="el-icon-rank" @click="changeEvent('move')"></i>
    </el-tooltip>
    <el-tooltip effect="dark" content="" placement="bottom">
      <i class="el-icon-full-screen" @click="changeEvent('fullScreen')"></i>
    </el-tooltip>
    <el-tooltip effect="dark" content="" placement="bottom">
      <el-popconfirm
        confirmButtonText=""
        cancelButtonText=""
        icon="el-icon-info"
        iconColor="red"
        @onConfirm="changeEvent('clearAll')"
        title=""
      >
        <i class="el-icon-delete" slot="reference"></i>
      </el-popconfirm>
    </el-tooltip>
    <el-tooltip effect="dark" content="" placement="bottom">
      <i class="el-icon-setting" @click="configDialogVisiable = true"></i>
    </el-tooltip>
    <div class="status">
      {{this.currentStatus}}
    </div>
    
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="configDialogVisiable"
      width="400px"
      :show-close = "false"
    >
      <el-form label-position="left" label-width="100px" size="mini" :model="config">
        <el-form-item label="">
          <el-color-picker v-model="config.path_strokeStyle" size="mini"></el-color-picker>
        </el-form-item>
        <el-form-item label="">
          <el-color-picker v-model="config.point_strokeStyle" size="mini"></el-color-picker>
        </el-form-item>
        <el-form-item label="">
          <el-input-number size="mini" v-model="config.path_lineWidth"></el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-input-number size="mini" v-model="config.point_lineWidth"></el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-input-number size="mini" v-model="config.point_radis"></el-input-number>
        </el-form-item>
        <el-form-item label="">
          <el-switch
            v-model="config.grid"
            active-color="#66a6ff"
            inactive-color="#999"
          ></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="config.guid" active-color="#66a6ff" inactive-color="#999"></el-switch>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="configDialogVisiable = false"> </el-button>
        <el-button size="small" type="primary" @click="configDialogVisiable = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "topbar",
  props:{
    currentStatus: String
  },
  data() {
    return {
      configDialogVisiable: false,
      // 
      config: {
        path_lineWidth: 1,
        path_strokeStyle: "#f00",
        point_lineWidth: 1,
        point_strokeStyle: "#999",
        point_radis:5,
        grid: false,
        guid: true
      }
    };
  },
  watch:{
    config: {
      handler() {
        this.$emit('configChange',this.config)
      },
      immediate:true,
      deep:true
    }
  },
  methods: {
    changeEvent(eventName) {
      this.$emit("topBarEvent", eventName);
    }
  },
};
</script>

<style lang="scss" scoped>
.bar {
  height: 30px;
  margin: 5px 0px;
}
.icon {
  height: 30px;
  width: 30px;
}
[class*=" el-icon-"],
[class^="el-icon-"] {
  box-sizing: border-box;
  height: 30px;
  width: 30px;
  line-height: 30px;
  font-size: 20px;
  padding: 0 5px;
  cursor: pointer;
  &:nth-child(1) {
    margin-left: 5px;
  }
  &:hover {
    border-radius: 3px;
    background-color: rgba(0, 0, 0, 0.4);
  }
}
.el-form-item--mini.el-form-item, .el-form-item--small.el-form-item{
  margin-bottom: 2px;
}
.status{
  float:right;
  margin-top: 8px;
  width: 160px;
  font-size: 14px;
}
</style>